:root {
    --dg: #101212;
    --lg: #C0B0B5;
    --g: #0F6;
    --c: #0FF;
    --m: #F03;
    --p: #0FF;
    --primary: #7fff00; /* */
    --onprimary: #000000; /* */
    --secondary: #000000; /* */
    --onsecondary: #000000; /* */
    --tertiary: #1f1fff; /* */
    --ontertiary: #ffffff; /* */
    --page: #fcf340; /* */
    --onpage: #ffffff; /* */
    --detail: #03dddc; /* */
    --detail2: #fb33db; /* */
    --speed: 500ms;
  
    /*fb33db Verdana, sans-serif;*/
  
    --font: 1rem Cyberway Riders;
    --header-font: lighter 6rem Cyberway Riders;
    --title-font: lighter 2rem "Monoton", cursive;
    --quote-font: lighter 1.5rem "Monoton", cursive;
    
  }
  
  @keyframes colorshift {
    0% {
      --p: var(--g);
    }
    50% {
      --p: var(--c);
    }
    100% {
      --p: var(--m);
    }
  }
  
  html,
  body {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    margin: 0;
    background-color: var(--dg);
    font-family: 'Aldrich', sans-serif;
    color: var(--lg);
    transition: color 1s;
    animation: colorshift 5s infinite;
    animation-direction: alternate;
  }
  
  .cred-container {
    font-family: 'Aldrich', sans-serif;
    width: fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    border-radius: 10px;
    box-shadow: 2px 2px 1px var(--p), 4px 4px 0 var(--dg), 6px 6px 1px var(--p);
  }
  
  .float {
    margin: auto;
  }
  
  .fixed {
    width: 100px;
    height: 50px;
    background-color: transparent;
  }
  
  .stack-g {
    padding: 10px;
    border-radius: 10px;
    box-shadow: 2px 2px 1px var(--p), 4px 4px 0 var(--dg), 6px 6px 1px var(--p);
  }
  
  input,
  button {
    display: block;
    border-radius: 5px;
    border-style: none;
    padding: 5px;
    margin: 5px;
    background-color: var(--dg) !important;
    box-shadow: 1px 1px 1px var(--p) inset;
    font-size: 1.5em;
    font-family: 'Aldrich', sans-serif;
    font-weight: bold;
    color: var(--lg);
    outline: none;
    /* text-shadow: 2px 2px 1px var(--p); */
  }
  
  input:hover,
  button:hover {
    box-shadow: 2px 2px 2px var(--p) inset;
  }
  
  button {
    display: inline-block;
  }
  
  .error-text {
    text-shadow: 1px 1px 2px #F00;
  }
  
  .inline {
    display: inline-block;
    vertical-align: middle;
    align-content: center;
  }
  
  .divider {
    border-radius: 5px;
    border-style: solid;
    border-width: 2px;
    border-color: var(--dg);
    height: 100px;
    box-shadow: 2px 2px 1px var(--p);
  }